import {
  Component,
  EventEmitter,
  Inject,
  OnInit,
  Output,
  Input
} from '@angular/core';
import { MatDatepicker } from '@angular/material';
import { FormBuilder, AbstractControl } from '@angular/forms';
import { slideDownAnimation } from '@core/animations';
import { Filter, Filters } from '../../models/filters';
import { MOMENT } from '@core/services';

@Component({
  selector: 'app-chart-filter',
  templateUrl: 'chart-filter.component.html',
  styleUrls: ['./chart-filter.component.scss'],
  animations: [slideDownAnimation]
})
export class ChartFilterComponent implements OnInit {
  activeFilter: Filter;

  customFilterControls: AbstractControl;

  @Input()
  disableFilter: boolean;

  @Input()
  enabled: Array<boolean> = [];

  filters: Filters = [
    {
      name: 'Custom',
      active: false,
      enabled: true,
      filterKey: 'custom'
    },
    {
      name: 'Day',
      active: false,
      enabled: false,
      filterKey: 'today'
    },
    {
      name: 'W',
      active: false,
      enabled: true,
      filterKey: 'week'
    },
    {
      name: 'M',
      active: false,
      enabled: true,
      filterKey: 'month'
    },
    {
      name: '3M',
      active: false,
      enabled: true,
      filterKey: '3months'
    },
    {
      name: 'All',
      active: false,
      enabled: true,
      filterKey: 'all'
    }
  ];

  @Output()
  onFilterChange: EventEmitter<Filter> = new EventEmitter();

  constructor(@Inject(MOMENT) private moment, private fb: FormBuilder) {
    this.customFilterControls = this.fb.group({
      fromDate: new Date(),
      toDate: new Date()
    });
  }


  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  getDates() {
    const dates = <{ endDt: string; startDt: string }>{};
    const formValue = this.customFilterControls.value;
    dates.endDt = this.moment(formValue.toDate).format('YYYY-MM-DD');
    dates.startDt = this.moment(formValue.fromDate).format('YYYY-MM-DD');
    return dates;
  }

  ngOnInit() {
    this.enabled.forEach((value, index) => {
      if (this.filters[index]) {
        this.filters[index].enabled = value;
      }
    });
    const lastEnabledFilter = this.filters
      .slice()
      .reverse()
      .find(val => val.enabled);
    lastEnabledFilter.active = true;

    this.activeFilter = lastEnabledFilter;

    this.onFilterChange.emit(this.activeFilter);
    this.customFilterControls.valueChanges.subscribe(_ => {
      const filter = Object.assign({}, this.activeFilter);
      const dates = this.getDates();
      filter.endDt = dates.endDt;
      filter.startDt = dates.startDt;
      this.onFilterChange.emit(filter);
    });
  }

  toggleFilter(filter: Filter) {
    if (
      this.disableFilter ||
      this.activeFilter.filterKey === filter.filterKey
    ) {
      return;
    }
    this.filters.forEach(val => (val.active = false));
    filter.active = true;
    this.activeFilter = filter;
    const _filter = Object.assign({}, this.activeFilter);
    if (filter.filterKey === 'custom') {
      const dates = this.getDates();
      _filter.endDt = dates.endDt;
      _filter.startDt = dates.startDt;
    }
    this.onFilterChange.emit(_filter);
  }



}
