import { Component, OnInit } from '@angular/core';
import { ReportsApiServce } from '@core/services/reports.service';
import { MisService } from '@app/modules/mis/services/mis.service';
import { ToastrService } from 'ngx-toastr';
import { UserService, UtilService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { GridApi, ColumnApi, GridOptions, GridReadyEvent, ColDef } from 'ag-grid/main';
import { Filter } from '@app/modules/mis/models/filters';
import { finalize } from 'rxjs/operators';
import { IOrgWisePharmacyProfits } from '@app/modules/mis/models/org-wise-pharmacy-profits';

@Component({
  selector: 'app-pharmacyProfit',
  styleUrls: ['./pharmacy-profit.component.scss'],
  templateUrl: 'pharmacy-profit.component.html'
})

export class PharmacyProfitComponent implements OnInit {
  activeFilter: Filter;
  columnDefs: Array<ColDef>;
  rowData = [];

  private sessionData: UserSession;
  private gridApi: GridApi;
 
  constructor(
    private reportsApiService: ReportsApiServce,
    private userService: UserService,
    private misService: MisService,
    private toastr: ToastrService
  ) {
    this.sessionData = this.userService.getUser();
  }

  initColDefs() {
    this.columnDefs = [
      { headerName: 'Drug Brand Name', field: 'drugBrandName' },
      { headerName: 'Purchase Cost', field: 'purchaseCost' },
      { headerName: 'Sale Amount', field: 'saleAmt' },
      { headerName: 'Profit', field: 'profit' }
    ];
  }

  fetchOrgWisePharmacyProfits() {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      authenticatedUserId: this.sessionData.regId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt,
    };

    if (this.gridApi) {
      this.gridApi.showLoadingOverlay();
    }

    this.reportsApiService.fetchOrgWisePharmacyProfits<Array<IOrgWisePharmacyProfits>>(payload)
      .pipe(
        finalize(() => {
          if (this.gridApi) {
            this.gridApi.hideOverlay();
          }
        }) 
      )
      .subscribe(
        (response) => {
          if (response.responseCode === 'E200') {
            this.rowData = response.data;
          } else if (response.responseCode === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
    );
  }

  onGridReady(event: GridReadyEvent) {
    this.gridApi = event.api;
    this.fetchOrgWisePharmacyProfits();
  }

  onFilterChange($event: Filter) {
    this.activeFilter = $event;
    this.fetchOrgWisePharmacyProfits();
  }

  ngOnInit() {
    this.initColDefs();
  }

}
