import { Component, OnInit, Inject, ChangeDetectorRef } from '@angular/core';
import { MisService } from '@app/modules/mis/services/mis.service';
import { ToastrService } from 'ngx-toastr';
import { HIGHCHARTS, UserService, UtilService } from '@core/services';
import { finalize } from 'rxjs/operators';
import { Filter } from '@app/modules/mis/models/filters';
import { UserSession } from '@core/models/user-session';
import { ReportsApiServce } from '@core/services/reports.service';
import { BranchWiseInvoiceCountResponse } from '@app/modules/mis/models/branch-wise-invoice-count';
import { OrgWiseInvoiceCountResponse } from '@app/modules/mis/models/org-wise-invoice-count';


@Component({
  selector: 'app-financial-invoices',
  templateUrl: 'financial-invoices.component.html',
  styleUrls: ['./financial_invoices.component.scss']
})
export class FinancialInvoicesComponent implements OnInit {
  activeFilter: Filter;
  chartConfig: any;
  currentDrillDownLevel = 0;
  chartInstance: any;
  updateChart = true;
  userSession: UserSession;
  invoiceList: any;

  constructor(
    @Inject(HIGHCHARTS) public highchartInst,
    private reportsApiService: ReportsApiServce,
    private userService: UserService,
    private utilService: UtilService,
    private misService: MisService,
    private toastr: ToastrService) {
    this.userSession = this.userService.getUser();
  }

  drilldownHandler($event) {
    switch ($event.point.level) {
      case 1: this.fetchOrgWiseInvoiceCount($event); break;
      case 2: this.fetchOrgModuleWiseInvoiceCount($event); break;
      case 3: this.fetchOrgWiseMonthInvoiceCount($event); break;
    }

  }

  drillUp() {
    this.chartInstance.drillUp();
  }

  fetchOrgWiseInvoiceCount(event) {
    const chartInst = event.target;
    const payload = {
      orgId: event.point.options.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt
    };
    chartInst.showLoading('Loading Data');
    this.reportsApiService
      .fetchOrgWiseInvoiceCount<OrgWiseInvoiceCountResponse>(payload)
      .pipe(
        finalize(() => {
          chartInst.hideLoading();
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          const series = {
            name: 'Module wise Invoice Count',
            id: event.point.drilldown,
            data: response.data.orgLevelInvoicesList.map((val) => {
              return Object.assign({}, val, { drilldown: this.utilService.generateUniqueId(), level: 2, type: val.name });
            })
          };
          chartInst.addSeriesAsDrilldown(event.point, series);
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      }, _ => this.toastr.error('Error occurred'));
  }

  //second drill down
  fetchOrgModuleWiseInvoiceCount(event) {
    const chartInst = event.target;
    const payload = {
      orgId: event.point.options.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt,
      type: event.point.type
    };
    chartInst.showLoading('Loading Data');
    this.reportsApiService
      .fetchOrgModuleWiseInvoiceCount<OrgWiseInvoiceCountResponse>(payload)
      .pipe(
        finalize(() => {
          chartInst.hideLoading();
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          const series = {
            name: 'Month wise Invoice Count',
            id: event.point.drilldown,
            data: response.data.orgLevelInvoicesList.map((val) => {

              const { drilldown, ...rest } = val;

              return Object.assign({}, rest, {
                ...((this.activeFilter.filterKey === '3months')? {
                  level: 3,
                  drilldown: this.utilService.generateUniqueId()
                } : null)
              })

            })
          };
          chartInst.addSeriesAsDrilldown(event.point, series);
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      }, _ => this.toastr.error('Error occurred'));
  }

  //third drill down
  fetchOrgWiseMonthInvoiceCount(event) {
    // console.log(event);
    const chartInst = event.target;
    const payload = {
      orgId: event.point.options.orgId,
      // orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt,
      type: event.point.options.type,
      startDate: event.point.options.startDate,
      yearMonth: event.point.options.yearMonth
    };
    chartInst.showLoading('Loading Data');
    this.reportsApiService
      .fetchOrgWiseMonthInvoiceCount<OrgWiseInvoiceCountResponse>(payload)
      .pipe(
        finalize(() => {
          chartInst.hideLoading();
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          const series = {
            name: 'Day Wise Invoice Count',
            id: event.point.drilldown,
            data: response.data.orgLevelInvoicesList.map((val) => {
              const { drilldown, ...rest } = val;
              return Object.assign({}, rest);
            })
          };
          // this.invoiceList = response.data;
          //payload.startDate = response.data.orgLevelInvoicesList.startDate;
          // payload.yearMonth = response.data.orgLevelInvoicesList.yearMonth;

          chartInst.addSeriesAsDrilldown(event.point, series);
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      }, _ => this.toastr.error('Error occurred'));
  }


  fetchOrgGrpWiseInvoiceCount(filter: Filter) {

    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: filter.filterKey,
      startDt: filter.startDt,
      endDt: filter.endDt
    };

    if (this.chartInstance) {
      this.chartInstance.showLoading('Loading data');
    }

    this.reportsApiService
      .fetchOrgGrpWiseInvoiceCount<BranchWiseInvoiceCountResponse>(payload)
      .pipe(
        finalize(() => {
          if (this.chartInstance) {
            this.chartInstance.hideLoading();
          }
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          this.chartConfig = this.getChartObject();

          this.chartConfig.series[0].data = response.data.branchWiseInvoiceCount.map((val) => {
            const _val = {
              level: 1,
              name: val.name,
              orgId: val.orgId,
              y: val.y,
              drilldown: this.utilService.generateUniqueId()
            }
            return _val;
          });

          this.chartConfig.title.text = `Total Bills(${response.data.totalInvoiceCount})`;

        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      }, _ => this.toastr.error('Error occurred'));
  }

  getChartObject() {
    return this.misService.getChartConfigObject({
      chart: {
        height: 300,
        events: {
          drilldown: ($event) => {
            ++this.currentDrillDownLevel;
            this.drilldownHandler($event);
          },
          drillup: () => {
            --this.currentDrillDownLevel;
          }
        }
      },
      title: { text: 'Invoice Details' },
      yAxis: {
        title: { text: 'Total Bills' }
      },
      series: [{
        name: 'Branches',
        data: []
      }]
    });
  }


  ngOnInit() {
    this.chartConfig = this.getChartObject();
  }

  onChartCreatedFactory() {
    return (chart) => {
      this.chartInstance = chart;
    };
  }

  onFilterChange($event: Filter) {
    this.activeFilter = $event;
    this.fetchOrgGrpWiseInvoiceCount($event);
  }
}
