import { Component, OnInit, Inject } from '@angular/core';
import { MisService } from '@app/modules/mis/services/mis.service';
import { ToastrService } from 'ngx-toastr';
import { HIGHCHARTS, UserService, UtilService } from '@core/services';
import { finalize } from 'rxjs/operators';
import { Filter } from '@app/modules/mis/models/filters';
import { UserSession } from '@core/models/user-session';
import { ReportsApiServce } from '@core/services/reports.service';
import { BranchWiseOpdInvoiceCountResponse } from '@app/modules/mis/models/branch-wise-opd-invoice-count';
import { OrgWiseIpdInvoiceCountResponse } from '@app/modules/mis/models/org-wise-ipd-invoice-count';
import { MonthWiseOpdCountResponse } from '@app/modules/mis/models/month-wise-opd-invoice-count';

@Component({
  selector: 'app-financial-opd',
  templateUrl: 'financialOpd.component.html',
  styleUrls: ['./financialOpd.component.scss']
})
export class FinancialOpdComponent implements OnInit {
  activeFilter: Filter;
  chartConfig: any;
  currentDrillDownLevel = 0;
  chartInstance: any;
  updateChart = true;
  userSession: UserSession;
  invoiceList: any;
  constructor(
    @Inject(HIGHCHARTS) public highchartInst,
    private reportsApiService: ReportsApiServce,
    private userService: UserService,
    private utilService: UtilService,
    private misService: MisService,
    private toastr: ToastrService
  ) {
    this.userSession = this.userService.getUser();
  }
  drilldownHandler($event) {
    switch ($event.point.level) {
      case 1: this.fetchOrgWiseOPDCollections($event); break;
      case 2: this.fetchMonthWiseOPDCollections($event); break;
    }
  }

  drillUp() {
    this.chartInstance.drillUp();
  }

  //api for month wise opd collections(second drilldown)

  fetchMonthWiseOPDCollections(event) {
    const chartInst = event.target;
    const payload = {
      orgId: event.point.options.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt,
      type: event.point.options.type,
      startDate: event.point.options.startDate,
      yearMonth: event.point.options.yearMonth

    };
    chartInst.showLoading('Loading Data');
    this.reportsApiService
      .fetchMonthWiseOPDCollections<MonthWiseOpdCountResponse>(payload)
      .pipe(
        finalize(() => {
          chartInst.hideLoading();
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          const series = {
            name: 'Branch Month Invoice Count',
            id: event.point.drilldown,
            data: response.data.orgWiseCollectionsList.map((val) => {
              const { drilldown, ...rest } = val;
              return Object.assign({}, rest);
            }),
          };



          chartInst.addSeriesAsDrilldown(event.point, series);
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      }, _ => this.toastr.error('Error occurred'));

  }



  //api for org wise collections(first drilldown)

  fetchOrgWiseOPDCollections(event) {
    const chartInst = event.target;
    const payload = {
      orgId: event.point.options.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt
    };
    chartInst.showLoading('Loading Data');
    this.reportsApiService
      .fetchOrgWiseOPDCollections<OrgWiseIpdInvoiceCountResponse>(payload)
      .pipe(
        finalize(() => {
          chartInst.hideLoading();
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          const series = {
            name: 'Month wise Opd Invoice Count',
            id: event.point.drilldown,
            data: response.data.orgWiseCollectionsList.map((val) => {
              const { drilldown, ...rest } = val;

              return Object.assign({}, rest, {
                ...((this.activeFilter.filterKey === '3months') ? {
                  level: 2,
                  drilldown: this.utilService.generateUniqueId()
                } : null),
                ... { type: val.name }
              });

            })
          };
          chartInst.addSeriesAsDrilldown(event.point, series);
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      }, _ => this.toastr.error('Error occurred'));
  }

  fetchOrgGrpWiseOPDCollection(filter: Filter) {

    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: filter.filterKey,
      startDt: filter.startDt,
      endDt: filter.endDt
    };
    if (this.chartInstance) {
      this.chartInstance.showLoading('Loading data');
    }

    this.reportsApiService
      .fetchOrgGrpWiseOPDCollection<BranchWiseOpdInvoiceCountResponse>(payload)
      .pipe(
        finalize(() => {
          if (this.chartInstance) {
            this.chartInstance.hideLoading();
          }
        })
      )
      .subscribe((response) => {
        if (response.responseCode === 'E200') {
          this.chartConfig = this.getChartObject();

          this.chartConfig.series[0].data = response.data.branchwiseOPDCollections.map((val) => {

            const _val = {
              level: 1,
              name: val.name,
              orgId: val.orgId,
              y: val.y,
              drilldown: this.utilService.generateUniqueId()
            };

            return _val;
          });

          this.chartConfig.title.text = `Total Revenue Amount (  ${response.data.totalAmt})`;

        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }

      }, _ => this.toastr.error('Error occurred'));

  }

  getChartObject() {
    return this.misService.getChartConfigObject({
      chart: {
        height: 300,
        events: {
          drilldown: ($event) => {
            ++this.currentDrillDownLevel;
            this.drilldownHandler($event);
          },
          drillup: () => {
            --this.currentDrillDownLevel;
          }
        }
      },
      title: { text: 'Opd invoice Details' },
      yAxis: {
        title: { text: 'Total OP Revenue()' }
      },
      series: [{
        name: 'Branches',
        data: []
      }]
    });
  }

  ngOnInit() {
    this.chartConfig = this.getChartObject();
  }

  onChartCreatedFactory() {
    return (chart) => {
      this.chartInstance = chart;
    };
  }

  onFilterChange($event: Filter) {
    this.activeFilter = $event;
    this.fetchOrgGrpWiseOPDCollection($event);
  }
}
