import { Component, OnInit, Inject } from '@angular/core';
import { MisService } from '@app/modules/mis/services/mis.service';
import { ToastrService } from 'ngx-toastr';
import { HIGHCHARTS, UserService, UtilService } from '@core/services';
import { finalize } from 'rxjs/operators';
import { Filter } from '@app/modules/mis/models/filters';
import { UserSession } from '@core/models/user-session';
import { ReportsApiServce } from '@core/services/reports.service';
import { BranchWiseIpdOpdPharmacyCountResponse } from '@app/modules/mis/models/branch-wise-ipd-opd-pharmacy-count';

@Component({
  selector: 'app-financial-ipd-opd',
  templateUrl: 'financial-ipd-opd-pharmacy.component.html',
  styleUrls: ['./financial-ipd-opd-pharmacy.component.scss']
})
export class FinancialIpdOpdPharmacyComponent implements OnInit {
  activeFilter: Filter;
  chartConfig: any;
  currentDrillDownLevel = 0;
  chartInstance: any;
  updateChart = true;
  userSession: UserSession;
  invoiceList: any;
  constructor(
    @Inject(HIGHCHARTS) public highchartInst,
    private utilService: UtilService,
    private reportsApiService: ReportsApiServce,
    private userService: UserService,
    private misService: MisService,
    private toastr: ToastrService
  ) {
    this.userSession = this.userService.getUser();
  }

  drilldownHandler($event) {
    switch ($event.point.level) {
      case 0:
        this.fetchOrgModuleWiseIpdAndOpdAndPharmacyCollections($event);
        break;
    }
  }

  drillUp() {
    this.chartInstance.drillUp();
  }
  // @api for org module wise ipd,opd and pharmacy collections(first drilldown)

  fetchOrgModuleWiseIpdAndOpdAndPharmacyCollections(event) {
    const chartInst = event.target;
    const payload = {
      orgId: event.point.options.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: this.activeFilter.filterKey,
      startDt: this.activeFilter.startDt,
      endDt: this.activeFilter.endDt
    };
    chartInst.showLoading('Loading Data');
    this.reportsApiService
      .fetchOrgModuleWiseIpdAndOpdAndPharmacyCollections<
        BranchWiseIpdOpdPharmacyCountResponse
      >(payload)
      .pipe(
        finalize(() => {
          chartInst.hideLoading();
        })
      )
      .subscribe(
        response => {
          if (response.responseCode === 'E200') {
            const series = {
              name: 'Dept Wise Invoice Count',
              id: event.point.drilldown,
              data: response.data.orgWiseCollectionsList.map((val) => {
                const { drilldown, ...rest } = val;
                return rest;
              })
            };
            chartInst.addSeriesAsDrilldown(event.point, series);
          } else if (response.responseCode === 'E400') {
            this.toastr.error(response.responseMessage);
          }
        },
        _ => this.toastr.error('Error occurred')
      );
  }
  // @ api for org group wise ipd,opd and pharmacy collections
  fetchOrgGrpWiseIpdAndOpdAndPharmacyCollections(filter: Filter) {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      key: filter.filterKey,
      startDt: filter.startDt,
      endDt: filter.endDt
    };
    if (this.chartInstance) {
      this.chartInstance.showLoading('Loading data');
    }

    this.reportsApiService
      .fetchOrgGrpWiseIpdAndOpdAndPharmacyCollections<
        BranchWiseIpdOpdPharmacyCountResponse
      >(payload)
      .pipe(
        finalize(() => {
          if (this.chartInstance) {
            this.chartInstance.hideLoading();
          }
        })
      )
      .subscribe(
        response => {
          if (response.responseCode === 'E200') {
            this.chartConfig = this.getChartObject();

            this.chartConfig.series[0].data = response.data.orgWiseCollectionsList.map(
              val => {
                const _val = Object.assign({}, val);
                _val.level = 0;
                _val.drilldown = this.utilService.generateUniqueId();
                return _val;
              }
            );

            this.chartConfig.title.text = `Total Revenue Amount (  ${
              response.data.totalAmount
            })`;
          } else if (response.responseCode === 'E400') {
            this.toastr.error(response.responseMessage);
          }
        },
        _ => this.toastr.error('Error occurred')
      );
  }

  getChartObject() {
    return this.misService.getChartConfigObject({
      chart: {
        height: 300,
        events: {
          drilldown: $event => {
            ++this.currentDrillDownLevel;
            this.drilldownHandler($event);
          },
          drillup: () => {
            --this.currentDrillDownLevel;
          }
        }
      },
      title: { text: 'Ipd-Opd-Pharmacy  invoice Details' },
      yAxis: {
        title: { text: 'Total Revenue()' }
      },
      series: [
        {
          name: 'Branches',
          data: []
        }
      ]
    });
  }

  ngOnInit() {
    this.chartConfig = this.getChartObject();
  }

  onChartCreatedFactory() {
    return chart => {
      this.chartInstance = chart;
    };
  }

  onFilterChange($event: Filter) {
    this.activeFilter = $event;
    this.fetchOrgGrpWiseIpdAndOpdAndPharmacyCollections($event);
  }
}
