import { Component, Inject } from '@angular/core';
import { QualityApiService, UserService, SCROLL_CONTAINER } from '@core/services';
import { QualityResponseData } from '../../models/quality';
import { MisService } from '../../services/mis.service';
import { HIGHCHARTS, MOMENT } from '@core/services';
import { AbstractControl } from '@angular/forms';
import { MatDatepicker } from '@angular/material/datepicker';
import { UserSession } from '@core/models/user-session';
import { ToastrService } from 'ngx-toastr';
import { Filter } from '@app/modules/mis/models/filters';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-quality',
  templateUrl: 'quality.component.html'
})
export class QualityComponent {
  chartConfigs: Array<any>;
  customQualityFilterControls: AbstractControl;
  // custom day w m 3m all
  enabledFilters = [true, false, false, true, true, false];
  filterControl: AbstractControl;
  highcharts;
  userSession: UserSession;
  parentScrollContainer: HTMLElement;

  constructor(
    private qualityApiService: QualityApiService,
    private misService: MisService,
    private toastr: ToastrService,
    @Inject(HIGHCHARTS) highcharts,
    private userService: UserService,
    @Inject(MOMENT) private moment,
    @Inject(SCROLL_CONTAINER) private scrollContainer: HTMLElement
  ) {
    this.userSession = this.userService.getUser();
    this.highcharts = highcharts;
    this.getParentScrollContainer();
  }

  getParentScrollContainer() {
    this.parentScrollContainer = this.scrollContainer;
  }

  filterValueChangeHandler($event: Filter) {
    let payload = this.getCommonPayload();
    switch ($event.filterKey) {
      case '3months': payload = Object.assign(payload, this.getThreeMonthsFilterPayload());
        break;
      case 'custom': payload = Object.assign(payload, this.getCustomFilterPayload($event));
        break;
      case 'month': payload = Object.assign(payload, this.getMonthFilterPayload());
        break;
    }
    this.getQualityData(payload);
  }

  getCommonPayload() {
    return {
      orgId: this.userSession.orgId,
      orgGroupId: this.userSession.orgGroupId,
      selectedCategeory: 'DEPT'
    };
  }

  getMonthFilterPayload() {
    const now = this.moment();
    const then = this.moment().subtract(1, 'month');
    const payload = {
      toMonth: now.month() + 1,
      fromMonth: then.month() + 1,
      fromYear: then.year(),
      toYear: now.year(),
    };
    return payload;
  }

  getThreeMonthsFilterPayload() {
    const now = this.moment();
    const then = this.moment().subtract(3, 'month');
    const payload = {
      fromMonth: then.month() + 1,
      toMonth: now.month() + 1,
      fromYear: then.year(),
      toYear: now.year()
    };
    return payload;
  }

  getCustomFilterPayload($event: Filter) {
    const from = this.moment($event.startDt);
    const to = this.moment($event.endDt);
    const payload = {
      fromMonth: from.month() + 1,
      toMonth: to.month() + 1,
      fromYear: from.year(),
      toYear: to.year()
    };
    return payload;
  }

  getQualityData(payload) {
    this.qualityApiService
      .getMonthlyData<Array<QualityResponseData>>(payload)
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.chartConfigs = response.data.map(data => {
            const chartConfig = this.misService.getChartConfigObject({
              chart: {
                height: 250
              },
              title: { text: data.indicatorName },
              yAxis: { title: { text: data.misYAxisName } },
              series: [
                {
                  name: data.indicatorName,
                  data: data.dataList
                }
              ]
            });
            return chartConfig;
          });
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage, 'Failure!');
        }
      }, _ => this.toastr.error('Error occurred', 'Failure!'));
  }
}
