import { Injectable, Inject } from '@angular/core';
import { MERGE_DEEP } from '@core/services';

@Injectable()
export class MisService {
  constructor(
    @Inject(MERGE_DEEP) private mergeDeep
  ) { }

  getChartConfigObject(extender = {}) {
    const defaultConfig = {
      credits: { enabled: false },
      chart: {
        type: 'column',
        zoomType: 'x',
        panning: true,
        panKey: 'shift'
      },
      title: { text: '' },
      xAxis: { type: 'category' },
      boost: {
        useGPUTranslations: true,
        boostThreshold: 1
      },
      yAxis: {
        title: { text: '' }
      },
      legend: { enabled: false },
      plotOptions: {
        series: {
          animation: { duration: 2000 },
          colorByPoint: true,
          borderWidth: 0,
          dataLabels: {
            enabled: true,
            format: '{point.y}'
          }
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
        pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y}</b> of total<br/>'
      },
      series: [ ]
    };
    return this.mergeDeep(defaultConfig, extender);
  }

}
