import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { QualityComponent } from '@app/modules/mis/components/quality/quality.component';
import { OperationalComponent } from '@app/modules/mis/components/operational/operational.component';
import { MisShellComponent } from '@app/modules/mis/components/shell/mis-shell.component';
import { ClinicalComponent } from '@app/modules/mis/components/clinical/clinical.component';
import { FinancialComponent } from '@app/modules/mis/components/financial/financial.component';

export const MisRoutes: Routes = [
  {
    path: '',
    component: MisShellComponent,
    children: [
      {
        path: 'quality-dashboard',
        component: QualityComponent,
        data: {
          title: 'Quality Dashboard'
        }
      },
      {
        path: 'operational-dashboard',
        component: OperationalComponent,
        data: {
          title: 'Operational Dashboard'
        }
      },
      {
        path: 'clinical-dashboard',
        component: ClinicalComponent,
        data: {
          title: 'Clinical Dashboard'
        }
      },
      {
        path: 'financial-dashboard',
        component: FinancialComponent,
        data: {
          title: 'Financial Dashboard'
        }
      },
      { path: '', redirectTo: 'clinical-dashboard', pathMatch: 'prefix' }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(MisRoutes)],
  exports: [RouterModule]
})
export class MisRoutingModule {}
